.cont {
  position: relative;
  display: flex;
}
.cont .DropDown_change {
  cursor: pointer;
  padding: 10px;
  background-color: hsl(0, 0%, 38%);
  color: #fff;
  border: none;
}
.cont .DropDown_ChangeMenu {
  background-color: hsl(0, 0%, 38%);
  color: #fff;
  border: none;
  margin-left: 5px;
  padding: 10px;
  position: relative;
}
.cont .DropDown_ChangeMenu .DropDown_title {
  color: #fff;
}
.cont .DropDown_ChangeMenu .DropDown_menu {
  margin-top: 10px;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  display: none;
}
.cont .DropDown_ChangeMenu .DropDown_menu a {
  color: black;
  text-decoration: none;
  display: block;
}
.cont .DropDown_ChangeMenu .DropDown_menu a:hover {
  color: blue;
}
.cont .DropDown_ChangeMenu .DropDown_menu li {
  padding: 12px 16px;
}
.cont .DropDown_ChangeMenu .DropDown_menu.open {
  display: block;
  height: 160px;
}